function makearray(stopValue: number, startValue: number, steps: number): any[] {
  const arr = [];
  const step = (stopValue - startValue) / (steps - 1);
  for (let i = 0; i < steps; i += 1) {
    arr.push(Math.ceil(startValue + (step * i)));
  }
  arr.shift();
  arr.pop();
  return arr;
}
