











































































































































import { defineComponent, ref, computed, onMounted, watch } from '@vue/composition-api';
import _ from 'lodash';
import { snackbar } from '@/components/GlobalSnackbar';
import store from '@/store';
import type {
  DatasetRequest,
  DatasetListingWafer,
} from '@/types';
import { objectToArray, arrayToObject, generateRouteByQuery } from '@/utils';
import FreshDialog from '@/components/FreshDialog.vue';
import WaferInformationUpload from '@/filemenu/file/metadata/components/WaferInformationUpload.vue';
import { uploadWaferInformationMenu } from '@/filemenu/file/metadata/state';

const headers = [
  { text: 'Wafer ID', value: 'wafer_id' },
  { text: 'Generation', value: 'generation' },
];
const submenu = [
  {
    text: 'Upload Wafer CSV',
    icon: 'mdi-upload',
    tooltip: 'Upload Wafer CSV',
    color: 'primary',
    click: () => {
      uploadWaferInformationMenu.value = true;
    },
  },
];
export default defineComponent({
  name: 'WaferInformationViewer',
  props: ['query'],
  components: { FreshDialog, WaferInformationUpload },
  setup(props, ctx) {
    const router = ctx.root.$router;
    const currentRoute = computed(() => ctx.root.$route);
    const client = computed(() => store.state.client);
    const loading = ref(false);
    const search = ref<string | null>(null);
    const selected = ref<any>();
    const items = ref<any[]>([]);
    const detail = ref<any>();
    const readOnly = ref<boolean>(true);
    const hasChanged = ref<boolean>(false);
    const mainKeys = ref<string[]>(['wafer_id']);
    async function fetchData() {
      if (!client.value) {
        return;
      }
      items.value = [];
      search.value = null;
      loading.value = true;
      const payload = { params: { filter: null, options: null } };
      const dataset = await client.value.getWafers(payload);
      loading.value = false;
      items.value = dataset;
    }
    async function selectAction(item: any) {
      if (!client.value) return;
      loading.value = true;
      // console.log(item._id, item.date_acquired);
      const fltr = { _id: item._id };
      const payload = { params: { filter: JSON.stringify(fltr), options: null } };
      const [data] = await client.value.getWafers(payload);
      const keys = mainKeys.value;
      const unsorted = objectToArray(data);
      detail.value = _.sortBy(unsorted, (x) => !keys.includes(x.key));
      loading.value = false;
      hasChanged.value = false;
      readOnly.value = true;
    }
    function addNew() {
      const newDoc: any = {};
      let obj: any[] = [];
      if (!detail.value) [obj] = items.value;
      else obj = arrayToObject(detail.value);
      _.forIn(obj, (v: any | null, k) => {
        newDoc[k] = null;
      });
      detail.value = objectToArray(newDoc);
      readOnly.value = false;
      hasChanged.value = false;
    }
    async function duplicateAction(ev: any) {
      const obj = arrayToObject(detail.value);
      obj.wafer_id = null;
      obj._id = null;
      detail.value = objectToArray(obj);
      hasChanged.value = true;
      readOnly.value = false;
    }
    async function saveAction(ev: any) {
      // console.log(detail.value);
      if (!client.value) return;
      const obj = arrayToObject(detail.value);
      const exist = await client.value.checkWafer(obj.wafer_id);
      let hasWritten = false;
      if (exist) {
        const payload = [obj];
        const resp = await client.value.putWafers(payload);
        hasWritten = true;
        // if entry exists, ask with popup dialog to overwrite
        // snackbar.dispatch({ text: `Wafer id ${obj.wafer_id} exists in the database`, options: { right: true, color: 'error' } });
      } else {
        // just upsert
        const payload = [obj];
        const resp = await client.value.putWafers(payload);
        hasWritten = true;
      }
      if (hasWritten) {
        await fetchData();
        hasChanged.value = false;
        readOnly.value = true;
        snackbar.dispatch({ text: 'Item has been successfully saved.', options: { right: true, color: 'success' } });
      }
    }
    async function deleteAction(ev: any) {
      if (!client.value) return;
      const obj = arrayToObject(detail.value);
      const resp = await client.value.deleteWafer(obj.wafer_id);
      await fetchData();
      detail.value = null;
      snackbar.dispatch({ text: 'Item has been successfully deleted.', options: { right: true, color: 'success' } });
    }
    async function inputChanged(val: any, index: any) {
      detail.value[index].value = val;
      hasChanged.value = true;
    }
    function isEmpty(str: string) {
      return (!str || str.length === 0);
    }
    function validate(data: any) {
      const obj = arrayToObject(detail.value);
      return !isEmpty(obj.wafer_id);
    }
    onMounted(async () => {
      store.commit.setSubmenu(submenu);
      await fetchData();
    });
    return {
      headers,
      search,
      selected,
      loading,
      items,
      detail,
      hasChanged,
      readOnly,
      selectAction,
      inputChanged,
      saveAction,
      duplicateAction,
      deleteAction,
      addNew,
      fetchData,
      validate,
      mainKeys,
      uploadWaferInformationMenu,
    };
  },
});

